
.NoteListNav__list {
  list-style-type: none;
  margin: 130px 0 0;
  padding: 0;
}

.NoteListNav__button-wrapper {
  margin: 0 0 0 auto;
  max-width: 180px;
}

.NoteListNav__add-folder-button {
  margin: 20px auto;
}
.NoteListNav__add-folder-button:hover {
  background-color: #404040;
  color: white;
}

.NoteListNav__folder-link {
  background-color: black;
  color: #b5b3b3;
  display: block;
  font-family: var(--custom-font);
  font-size: 20px;
  margin: 0 0 8px auto;
  max-width: 180px;
  padding: 8px 16px;
  text-decoration: none;
  font-weight: bold;
}

.NoteListNav__folder-link:hover {
  color: black;
  cursor: pointer;
}
.NoteListNav__folder-link.active {
  background-color: #404040;
}

.NoteListNav__num-notes {
  color: #adadad;
  display: block;
  float: right;
  font-size: 10px;
}

.editFolder {
  font-size: .8rem;
  margin-top: .55rem;
  margin-left: 1rem;
  font-weight: bold;
}

.buttons {
  display: flex;
}

button {
  margin-top: .5rem;
  display: flex;
  height: 1rem;
  background-color: black;
  border: 0;
  color: #bdbcbc;
  font-size: .8rem;
}
button {
  background-color:black;
  color: white;
  cursor: pointer;
  font-weight: bold;
}
